html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}
.toggle-button:focus {
  outline: none;
}
.toggle-button__line {
  width: 40px;
  height: 1px;
  background: white;
}
@media (min-width: 700px) {
  .toggle-button__line {
    background: black;
    height: 2.5px;
    border-radius: 25px;
  }
}
.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  padding: 2rem 0;
}
.toolbar__navigation {
  width: 85%;
  margin: auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}
.toolbar__logo {
  width: 50px;
  height: 50px;
}
@media (min-width: 700px) {
  .toolbar {
    padding: 3rem 0;
  }
}
.footer {
  width: 100%;
  background: grey;
  font-size: 0.8rem;
  text-align: center;
  padding: 3rem 0;
}
.footer-icons {
  width: 55%;
  margin: 0 auto 2rem;
  display: flex;
  justify-content: space-around;
}
.fa:before {
  font-size: 1.7rem;
  color: white;
  cursor: pointer;
}
@media (min-width: 700px) {
  .footer {
    padding: 3em 10%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: none;
    color: grey;
    text-align: left;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
  }
  .footer-icons {
    width: 18%;
    margin: 0;
    justify-content: space-between;
  }
  .fa:before {
    color: grey;
  }
}

.side-drawer {
  height: 100%;
  background: pink;
  position: fixed;
  right: 0;
  bottom: 0;
  width: 70%;
  max-width: 400px;
  z-index: 2;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: -webkit-transform .5s ease-out;
  transition: transform .5s ease-out;
  transition: transform .5s ease-out, -webkit-transform .5s ease-out;
}
.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0)
}
.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.side-drawer li {
  margin: 0.5rem 0;
}
.side-drawer a{
  color: black;
  text-decoration: none;
  font-size: 1.2rem;
}
.side-drawer a:hover,
.side-drawer a:active {
  color: #fa923f;
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0 ,0.3);
  z-index: 1;
}
.wrap-white-line {
  margin-bottom: 1rem;
  opacity: 0;
  -webkit-animation: fade-in-right ease 1s forwards;
          animation: fade-in-right ease 1s forwards;
}
.wrap-white-line {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}
@-webkit-keyframes fade-in-right {
  from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes fade-in-right {
  from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}


.picture-section-wrap {
  position: relative;
  height: 90vh;
}
.wrap-hello {
  position: absolute;
  color: white;
  left: 0;
  bottom: 0;
  margin: 35% 15%;
}
.hello {
  font-size: 6rem;
}
.hello-subtitle {
  font-size: 1.8rem;
  font-family: "Poiret One";
}
@media (min-width: 700px) {
  .picture-section-wrap {
    width: 65%;
    height: 100vh;
  }
  .wrap-hello {
    margin: 20% 15%;
  }
  .hello {
    font-size: 8rem;
  }
}

.vertical-line {
  display: none;
}
@media(min-width: 700px) {
  .vertical-line {
    display: block;
    width: 1px;
    background: grey;
    height: 100vh;
    margin-left: 4px;
  }
  @-webkit-keyframes grow {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  @keyframes grow {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  .vertical-line {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-animation: grow cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s forwards;
            animation: grow cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s forwards;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
  }
}

.description-section-wrap {
  width: 85%;
  margin: 5rem auto;
}
.description-title {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}
.description-text {
  margin-bottom: 2rem;
}
@media (min-width: 700px) {
  .description-section-wrap {
    flex: 1 1;
    padding: 0 4rem;
  }
}

@media (min-width: 700px) {
  .home-wrap {
    display: flex;
    align-items: center;
  }
}






* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  font-family: Raleway;
}
main {
  flex: 1 1;
}

